.special {
  overflow: hidden;
  text-align: center;
}

.special__heading {
  align-items: center;
}

.special__wrapper {
  padding: 64px 0;
  display: flex;
  gap: 32px;
}

.special__wine, .special__cocktails {
  padding: 32px 0;
  flex-grow: 1;
  flex-shrink: 0;
}

.special__image {
  flex-grow: 0;
  flex-shrink: 1;
  max-width: 412px;
  align-self: flex-start;
}

.special__image img {
  max-width: 100%;
}

.special__title {
  font-family: var(--fontFamilyMain);
  font-weight: 600;
  font-size: 45px;
  line-height: 130%;
  text-align: center;
  text-transform: capitalize;
}

.special__list {
  max-width: 412px;
  padding-top: 48px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.menuElement {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-start;
}

.menuElement__heading {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 32px;
}

.menuElement__divider {
  margin-left: auto;
  width: 119px;
  height: 1px;
  background: #FAFAFA;
}

@media (max-width: 1000px) {
  .special__image {
    display: none;
  }

  .special__heading .title {
    text-align: center;
  }
  .special__wrapper {
    flex-direction: column;
    gap: 0;

  }

  .special__list {
    max-width: 100%;
  }
  .menuElement__title {
    flex-grow: 1;
    flex-shrink: 0;
  }
  .menuElement__divider {
    max-width: 100%;
    width: 100%;
  }

  .menuElement__bottom {
    text-align: left;
  }
}
