.section__laurels {
  background-image: url("../images/bg-laurels.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
}

.laurels__stamp {
  position: absolute;
  top: 40px;
  left: 45%;
}
.laurels {
  display: flex;
  align-items: center;
  gap: 80px;
  justify-content: space-between;
  overflow: hidden;
}

.laurels__text {
  flex-grow: 1;
  flex-shrink: 0;
}

.laurels__image {
  flex-shrink: 1;
  flex-grow: 0;
  position: relative;
}

.laurels__image--g {
  position: absolute;
  bottom: 0;
  left: -70px;
}

.laurels__image img {
  max-width: 100%;
  object-fit: cover;
}

.laurels__item {
  display: flex;
  align-items: center;
  gap: 32px;
  margin-bottom: 64px;
}

.laurels__item--wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-start;
  max-width: 190px;
}

.laurels__item--description {
  text-align: left;
}

.laurels__item--image {
  width: 80px;
  height: 80px;
}

.laurels__list {
  margin-top: 64px;
  display: flex;
  max-width: 640px;
  flex-wrap: wrap;
  gap: 32px;
}

@media (max-width: 1000px) {
  .laurels {
    flex-direction: column;
  }

  .laurels__list {
    max-width: 100%;
    justify-content: center;
    gap: 64px;
  }

  .laurels__text .titleComponent  {
    align-items: center;
  }

  .laurels__stamp {
    left: 40px;
  }
}

@media (max-width: 750px) {
  .laurels__item {
    width: 100%;
    gap: 50px;
    margin-bottom: 20px;
  }

  .laurels__item--wrapper {
    max-width: 100%;
  }

  .laurels__image--g {
    left: 0;
    opacity: 0.5;
  }

  .laurels__stamp {
    width: 100px;
    height: 100px;
    opacity: 0.4;
  }
}
