.footer__wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  overflow: hidden;
  gap: 70px;
  margin-bottom: 64px;
}

.footer__contact {
  text-align: center;
}

.footer__hours {
  margin-left: auto;
  text-align: right;
  white-space: nowrap;
}

.footer__contact--title {
  font-family: var(--fontFamilyMain);
  font-size: 32px;
  line-height: 130%;
  text-transform: capitalize;
  color: var(--goldenLight);
  margin-bottom: 24px;
}

.footer__contact--description {
  white-space: nowrap;
}

.footer__social--logo {
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  font-size: 64px;
  line-height: 130%;
  text-transform: uppercase;
  color: var(--colorMain);
  margin-bottom: 32px;
}

.footer__social {
  text-align: center;
}

.footer__social--description {
  margin-bottom: 16px;
}

.footer__social--list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-top: 16px;
}

.footer__social--link svg path {
  fill: var(--goldenLight);
  transition: fill .3s ease-out;
}

.footer__social--link:hover svg path {
  fill: var(--colorMain);
}

.footer__hours--title {
  font-family: var(--fontFamilyMain);
  font-size: 32px;
  line-height: 130%;
  text-transform: capitalize;
  color: var(--goldenLight);
  margin-bottom: 24px;
}

.footer__hours--detail {
  display: flex;
  flex-direction: column;
  gap: 8px;
  text-align: center;
}

.copy {
  text-align: center;
  width: 100%;
  display: inline-block;
}

@media (max-width: 980px) {
  .footer__wrapper {
    flex-direction: column;
  }

  .footer__contact, .footer__social, .footer__hours {
    width: 100%;
  }
  .footer__hours {
    text-align: center;
  }

  .footer__social {
    order: -1;
  }
}
