.hero__container {
  overflow: hidden;
}

.section__hero {
  padding-top: 50px;
  padding-bottom: 150px;
}
.hero {
  max-width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero__text {
  max-width: 455px;
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin-right: 30px;
}

.hero__description {
  text-align: left;
}

.hero__button {
  align-self: flex-start;
}

.hero__slider {
  width: 50%;
  margin: 0 !important;
}

.hero__slide {
  height: 100%;
  width: 100%;
  position: relative;
  padding: 24px;
}

.hero__slide::before {
  position: absolute;
  content: '';
  width: 60%;
  height: 60%;
  bottom: 0;
  left: 0;
  border-left: 24px solid var(--colorMain);
  border-bottom: 24px solid var(--colorMain);
}

.hero__slide::after {
  position: absolute;
  content: '';
  width: 60%;
  height: 60%;
  top: 0;
  right: 0;
  border-right: 24px solid var(--colorMain);
  border-top: 24px solid var(--colorMain);
}

.hero__slide img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  vertical-align: bottom;
}

.sliderBtn {
  font-family: var(--fontFamilyMain);
  font-weight: 600;
  font-size: 20px;
  line-height: 175%;
  text-transform: capitalize;
  color: var(--goldenLight);
  transition: color .3s ease-out, transform .1s ease-out;
  cursor: pointer;
  display: inline-block;
  padding: 0 2px;
}

.sliderBtn.active {
  color: white;
  transform: scale(1.1);
}

.sliderBtn:last-child span {
  display: none;
}

.sliderBtn span {
  color: var(--goldenLight);
  padding: 0 2px;
}

@media (max-width: 950px) {
  .section__hero {
    padding-bottom: 50px;
  }

  .hero {
    flex-direction: column;
    gap: 70px;
  }

  .hero__text {
    margin-right: 0;
    max-width: 100%;
  }

  .hero__slider {
    width: 100%;
  }

  .hero__slide img {
    width: 100%;
  }
}
