.section__gallery {
  padding-top: 45px;
  padding-bottom: 18px;
}

.gallery {
  max-width: 1895px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  display: flex;
  gap: 64px;
  align-items: flex-start;
  overflow: hidden;
}

.gallery__text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  max-width: 500px;
  text-align: left;
  flex-shrink: 0;
  flex-grow: 1;
}

.gallery__content {
  display: flex;
  gap: 32px;
  flex-grow: 0;
  flex-shrink: 1;
  width: calc(100% - 564px);
}

.gallery__item {
  flex-shrink: 1;
  flex-grow: 0;
  width: calc(25% - 16px);
  max-height: 447px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.gallery__item:before {
  content: '';
  position: absolute;
  inset: 0;
  background-color: rgba(0,0,0, 0.5);
  transform: translateY(100%);
  transition: transform .3s ease-out;
}

.gallery__item:hover.gallery__item:before {
  transform: translateY(0);
}

.gallery__item:after {
  content: '';
  position: absolute;
  background-image: url("../images/instagram_icon.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 52px;
  height: 52px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity .3s ease-out;
}

.gallery__item:hover.gallery__item:after {
  opacity: 1;
}

.gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1200px) {
  .gallery {
    flex-direction: column;
  }
  .gallery__content {
    width: 100%;
  }
}

@media (max-width: 750px) {
  .gallery__content {
    flex-wrap: wrap;
  }
  .gallery__item {
    max-width: none;
    width: calc(50% - 16px);
  }
  .gallery__item img {
    max-height: 100%;
    width: 100%;
  }
}
