.section__about {
  /*padding: 128px 0;*/
  background-image: url('../images/bg-aboutSection.png');
  background-position: center center;
  background-repeat: no-repeat;
  background-color: rgba(0,0,0, 0.9);
}

.about {
  max-width: 1340px;
  padding: 0 20px;
  margin: 0 auto;
  display: flex;
  gap: 85px;
  justify-content: space-between;
  background-image: url("../images/G.png");
  background-repeat: no-repeat;
  background-size: 40%;
  background-position: center center;
  overflow: hidden;
}

.aboutUs {
  max-width: 525px;
  min-height: 292px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 10rem 0;
}

.aboutUs svg {
  transform: rotate(180deg);
}
.about__description {
  padding: 32px 0;
  text-align: right;
}

.about__button {
  margin-top: auto;
}

.ourHistory .about__description {
  padding: 32px 0;
  text-align: left;
}

.ourHistory {
  max-width: 525px;
  min-height: 292px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 10rem 0;
}

.knife img {
  object-fit: cover;
  height: 100%;
  width: 84px;
}

@media (max-width: 1000px) {
  .about {
    flex-direction: column;
    gap: 85px;
  }

  .knife{
    display: none;
  }

  .aboutUs, .ourHistory {
    max-width: 100%;
    align-items: center;
    padding: 0;
  }

  .aboutUs .about__description, .ourHistory .about__description{
    text-align: center;
  }
}
