.subscribe__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 64px;
  padding-bottom: 64px;
  border: 1px solid rgba(220, 202, 135, 0.2);
  overflow: hidden;
}

.subscribe__container > div > .titleComponent {
  align-items: center;
  margin-bottom: 24px;
  max-width: 790px;
}
.subscribe__container > div > .titleComponent > .title {
  text-align: center;
}
.subscribe__form {
  margin-top: 64px;
  max-width: 790px;
  display: flex;
  gap: 32px;
  width: 100%;
  height: 60px;
}

@media (max-width: 600px) {
  .subscribe__form {
    flex-direction: column;
    gap: 20px;
  }
  .subscribe__button {
    width: 100%;
  }
}

.subscribe__input {
  width: 100%;
  padding: 16px 20px;
  font-family: var(--fontFamilyMain);
  font-size: 16px;
  background-color: transparent;
  color: white;
  border: 1px solid var(--goldenLight);
}

.subscribe__input::placeholder {
  color: var(--colorSecondary);
}

.subscribe__button {
  align-self: center;
  min-height: 44px;
  min-width: 124px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.subscribe__button:disabled {
  pointer-events: none;
  opacity: .7;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.spinner {
  width: 28px;
  height: 28px;
  border: 2px solid white;
  border-top-color: transparent;
  border-radius: 50%;
  animation: rotate 1s infinite;
}
