.titleComponent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.titleComponent__subtitle {
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  font-size: 23px;
  line-height: 130%;
  text-transform: capitalize;
  font-feature-settings: 'tnum' on, 'lnum' on;
}

.titleComponent__title {
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  font-size: 90px;
  line-height: 130%;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  color: var(--colorMain);
  text-align: left;
}

@media (max-width: 550px) {
  .titleComponent__title {
    font-size: 65px;
  }
}
