.section__chef {
  background-image: url("../images/bg-chefSection.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.chef__container {
  overflow: hidden;
}

.chef {
  display: flex;
  gap: 96px;
  align-items: center;
}

.chef__text {
  display: flex;
  flex-direction: column;
  gap: 64px;
  align-items: flex-start;
}

.chef__description {
  font-style: italic;
  text-align: start;
}

.chef__description span {
  margin-right: 10px;
}

.chef__name--title {
  font-family: var(--fontFamilyMain);
  font-size: 32px;
  line-height: 130%;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  color: var(--colorMain);
  margin-bottom: 8px;
  font-weight: 400;
}

.chef__autograph {
  font-family: var(--fontFamilyHandwriting);
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 130%;
  letter-spacing: 0.04em;
  color: var(--colorSecondary);
  margin-top: 64px;
  text-align: left;
}

.chef__image {
  max-width: 50%;
  align-self: flex-start;
}

@media (max-width: 1000px) {
  .chef {
    flex-direction: column-reverse;
    gap: 70px;
  }

  .chef__text {
    gap: 50px;
  }

  .chef__autograph {
    margin-top: 50px;
  }

  .titleComponent__chef {
    align-self: center;
    text-align: center;
  }

  .chef__image {
    max-width: 100%;
    max-height: 700px;
    object-fit: cover;
    align-self: center;
  }
}
