* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

button {
  background-color: transparent;
  color: inherit;
  cursor: pointer;
  border: none;
}



:root {
  --fontFamilyMain: 'Cormorant Upright';
  --fontFamilySecondaty: 'Open Sans';
  --fontFamilyHandwriting: 'Licorice';

  --colorMain: #DCCA87;
  --colorSecondary: #AAAAAA;
  --goldenLight: #F5EFDB;
  --colorBlack: #0C0B08;
}

body {
  letter-spacing: 0.04em;
  font-size: 16px;
  font-weight: 400;
  background-color: #0C0B08;
  color: #ffffff;
  font-family: var(--fontFamilySecondaty);
}

body.lock {
  overflow: hidden;
}

.container {
  max-width: 1700px;
  padding: 0 20px;
  margin: 0 auto;
}

.containerSecondary {
  max-width: 1340px;
  padding: 0 20px;
  margin: 0 auto;
}

.description {
  font-family: var(--fontFamilySecondaty);
  font-size: 16px;
  line-height: 175%;
  color: #AAAAAA;
}

.title {
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  font-size: 64px;
  line-height: 130%;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  color: var(--colorMain);
  text-align: left;
}

.heading {
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  font-size: 23px;
  line-height: 130%;
  text-transform: capitalize;
  color: var(--colorMain);
}

.subtitle {
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  font-size: 23px;
  line-height: 130%;
  text-transform: capitalize;
}

.button {
  z-index: 1;
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  padding: 8px 32px;
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  line-height: 175%;
  color: var(--colorBlack);
  background-color: var(--colorMain);
  border-radius: 3px;
  cursor: pointer;
  font-size: 16px;
}
.button:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0, 0.4);
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.button:hover, .button:focus, .button:active {
  color: rgb(255,255,255);
}
.button:hover:before, .button:focus:before, .button:active:before {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

.sectionPaddingY {
  padding: 128px 0;
}

@media (max-width: 1000px) {
  .sectionPaddingY {
    padding: 60px 0;
  }
}
