.video video {
  height: 100vh;
  width: 100%;
  object-fit: cover;
}

.video {
  position: relative;
}

.video__overlay {
  position: absolute;
  inset: 0;
  background: rgba(0,0,0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.video__control {
  width: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--colorMain);
  border-radius: 50%;
  cursor: pointer;
}
