.header {
  display: flex;
  align-items: center;
  padding: 32px 20px;
  justify-content: space-between;
  max-width: 1700px;
  margin: 0 auto;
}

.logo {
  font-family: var(--fontFamilyMain);
  font-weight: 700;
  font-size: 44px;
  line-height: 130%;
  text-transform: uppercase;
}

.nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 32px;
}

.nav__link {
  font-family: 'Open Sans';
  line-height: 175%;
  padding: 3px 0;
  position: relative;
  transition: color 0.3s ease-out;
}
.nav__link:hover {
  color: var(--goldenLight);
}
.nav__link::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  background: var(--goldenLight);
  height: 2px;
  -webkit-transition-property: right;
  transition-property: right;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.nav__link:hover:before, .nav__link:focus:before, .nav__link:active:before {
  right: 0;
}

.controls {
  display: flex;
  align-items: center;
  gap: 50px;
  height: 57px;
}

.controls br {
  display: none;
}

.controls button {
  font-family: 'Open Sans';
  font-weight: 600;
  line-height: 175%;
  letter-spacing: 0.05em;
  text-transform: capitalize;
  transition: color 0.3s ease-out;
  position: relative;
  padding: 5px 0;
  font-size: 16px;
}

.controls button:hover {
  color: var(--goldenLight);
}
.controls button::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  background: var(--goldenLight);
  height: 2px;
  -webkit-transition-property: right;
  transition-property: right;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.controls button:hover:before, .controls button:focus:before, .controls button:active:before {
  right: 0;
}

.divider {
  width: 1px;
  height: 100%;
  background: radial-gradient(circle at center, white, transparent);
}

.burger {
  height: 25px;
  width: 35px;
  position: relative;
  overflow: hidden;
  z-index: 10;
  display: none;
}

.burger div {
  width: 100%;
  height: 2px;
  background: white;
  transition: transform .3s ease-out;
}

.burger div:nth-child(1) {
  position: absolute;
  transform: translateX(-40%);
  bottom: 0;
}
.burger div:nth-child(2) {
  position: absolute;
  top: calc(50% - 1px);
  transform: translateX(-20%);
}
.burger div:nth-child(3) {
  position: absolute;
  top: 0;
}

.burger:hover div, .burger:focus div {
  transform: translateX(0);
}

@media (max-width: 1300px) {
  .nav {
    gap: 20px;
  }
  .controls {
    gap: 20px;
  }

  .logo {
    font-size: 30px;
  }
}

@media (max-width: 950px) {
  .burger {
    display: block;
  }

  .header {
    justify-items: flex-start;
  }

  .logo {
    font-size: 26px;
  }

  .nav {
    gap: 50px;
    position: absolute;
    flex-direction: column;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 20px;
    justify-content: center;
    transform: translateY(-101%);
    transition: transform .3s ease-out;
    z-index: 9;
    background-color: rgba(0,0,0, 0.9);
    flex-wrap: nowrap;
    overflow: auto;
    min-height: 100vh;
  }

  .nav.open {
    transform: translateY(0);
  }

  .nav__link {
    font-size: 44px;
  }

  .controls {
    gap: 15px;
    margin-left: auto;
    margin-right: 30px;
  }

  .controls button {
    font-size: 14px;
  }
}

@media (max-width: 550px) {
  .logo {
    margin-right: 15px;
    font-size: 22px;
  }

  .controls {
    margin-right: 20px;
  }

  .controls button {
    font-size: 10px;
  }

  .controls br {
    display: block;
  }
}

