.contact__container {
  display: flex;
  align-items: center;
  gap: 120px;
  overflow: hidden;
}

.contact__text {
  flex-basis: 515px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contact__image {
  flex-basis: 667px;
}

.contact__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contact__text--description {
  padding: 32px 0 32px 0;
}

.contact__text--hours {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.hours__title {
  margin-bottom: 12px;
}

.hours__detail {
  line-height: 250%;
  margin-bottom: 50px;
}

.section__contact {
  background-image: url("../images/contact-bg.png");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: cover;
}

@media (max-width: 740px) {
  .contact__container {
    flex-direction: column;
    gap: 100px;
  }

  .contact__text, .contact__image {
    flex-basis: 100%;
  }
}
